<template>
    <div class="show-Graph">
      <div style="margin:auto; width:400px; padding: 10px;" id="popup" ref="popup"></div>

      <template>
        <el-dialog title="知识图谱" :close-on-click-modal="false" :append-to-body="true" :close-on-press-escape="false" :visible.sync="dialogshowGraph" :before-close="dialog_close" :modal-append-to-body="false">
          <div id="chartContainer" ref="chartContainer"></div>
        </el-dialog>
      </template>      
    </div>    
</template>

<script>
  //import { NetChart } from "@dvsl/zoomcharts"
  import {entityGraph} from '../../axios/api' 
  import { mapState } from 'vuex'
    export default {
      name: 'showGraph',
      data () {
          return {
            formLabelWidth: '120px',
            chart: null,
            nodeIdForPopup: null,
            nodeDataForPopup: null,
            nodeKeyAliasDict:{
              "person":"人物",
              "location":"地点",
              "time":"时间",
              "t_url":"链接",
              "t_title":"标题",
              "content":"内容",
              "position":"职位",
              "resume":"简历",
              "nation":"国籍",
            },
            nodeKeyNotDisplay:{
              "t_id":0,
              "trigger":0,
              "value":0,
              "target":0,  
              "end_time":0,
              "weight":0, 
            },
          }
      },
      computed:{
        ...mapState({
          dialogshowGraph:state=>state.dialogshowGraph,
          graph_data:state=>state.graph_data,
        })
      },
      updated(){
        this.init_graph()
      },
      methods: {       
        dialog_close() {
          this.$store.dispatch('dialogshowGraph');
          this.$refs.popup.style.display = "none";
        },
        reqExpandNode(node_id, value, label, obj){
          entityGraph(value,label).then(res=>{ 
            obj.chart.addData(res.data);            
          })
        },     
        update(e,obj) {
          if(!obj.dialogshowGraph) return;
          var popup = obj.$refs.popup;
          if(!obj.nodeIdForPopup) {
              if(!(popup.offsetWidth === 0 && popup.offsetHeight === 0)) {
                  popup.style.display = "none";
              }
              return;
          }
          //get latest node clicked/dragged or initial one
          var nodeId = obj.nodeIdForPopup;
          
          //if other node clicked/dragged:
          if(e && e.clickNode) {
              nodeId = e.clickNode.id;
              //update nodeId to the latest node clicked/dragged
              obj.nodeIdForPopup = nodeId;
          }

          var dimensions = obj.chart.getNodeDimensions(nodeId);
          if(!dimensions || !nodeId) {
              return;
          }

          var container = obj.$refs.chartContainer;
          var off_top = container.offsetTop;
          var off_left = container.offsetLeft;

          //updte popup position and show it if it's hidden.
          popup.style.top = off_top + dimensions.y + "px";
          popup.style.left = off_left + dimensions.x + "px";
          popup.style.display = "block";
        },
        init_graph() {
          var obj = this;
          obj.nodeIdForPopup = null;
          obj.added_graph_data = null;
          this.chart = new NetChart({
            container: this.$refs.chartContainer,
            info:{
              enabled: true,
              nodeContentsFunction: function(itemData, item){
              },
              linkContentsFunction: function(itemData, item){
                if(itemData.name){
                  return  "<p style='padding-left:5px;padding-right:5px;'>" + itemData.name + "</p></div>";
                }                  
              }
            },          
            events: {
              onClick:function(e, args) {
                if(args.clickNode) {
                    if(obj.nodeIdForPopup != args.clickNode){
                      obj.nodeIdForPopup = args.clickNode;
                      obj.nodeDataForPopup = args.clickNode.data;
                    }                                        
                } else {
                    obj.nodeIdForPopup = null;
                }
                obj.$options.methods.update(e,obj);
              },
              onPositionChange: function(e, args){obj.$options.methods.update(e,obj)},
              onPointerDrag: function(e, args){obj.$options.methods.update(e,obj)},
              onChartUpdate: function(e, args){obj.$options.methods.update(e,obj)},
              onSettingsChange: function(e, args){obj.$options.methods.update(e,obj)},
              onDoubleClick: function(e, args){obj.reqExpandNode(args.clickNode.data.id, args.clickNode.data.props.value, args.clickNode.data.label,obj);e.preventDefault();},
            },           
            style:{
              node:{display:"roundtext"},
              nodeStyleFunction:function nodeStyle(node){ 
                var label = node.data.style.label;
                var slice_len = 4;
                var array = new Array();
                var len = label.length;
                for(var i=0;i<(len/slice_len);i++){
                  if((i+1)*slice_len > len){
                  array.push(label.substring(i*slice_len,len));
                  }else{
                      array.push(label.substring(i*slice_len,(i+1)*slice_len));
                  }
                }
                label = array.slice(0,2).join(' ');
                if(array.length>2) label+="...";
                  
                node.label = label;
                node.items = [{text:node.data.label, px:0, py:-1, x:0, y:-5, textStyle:{fillColor:"black"}}];
                if (node.hovered){
                    node.radius = 50;                    
                }else{
                    node.radius = 40;
                }
              }
            },      
          });          
          this.chart.addData(this.graph_data);
          
        },        
      },
      watch:{
        graph_data(){
          this.init_graph();
        },
        nodeDataForPopup(){
          var itemData = this.nodeDataForPopup
          var popup = "<div style='margin:auto; width:400px; height:100%; padding: 10px;'>" + 
                  "<h3 style='font-weight: 300; font-size: 18px; color: #2f256e; padding-bottom: 3px; margin:0px'>"+ 
                  itemData.props.value + "[" + itemData.label + "]</h3>";
          for (let key in itemData.props) {
            //是否显示属性
            if(key in this.nodeKeyNotDisplay) continue;
            //是否json属性
            var prop_value = itemData.props[key];
            try {
              var obj=JSON.parse(prop_value);
              prop_value = "<a href='" + itemData.props['t_url'] + "' target='_blank'>" + "请点击链接查看具体内容" + "</a>";
            } catch(e) {
              prop_value = prop_value;
            }

            var prop_key = key;
            if(key in this.nodeKeyAliasDict){
              prop_key = this.nodeKeyAliasDict[key];
            }
            //是否为url
            if(key == 't_url'){
              if(itemData.label == "Person") continue;
              var line = "<a href='" + prop_value + "' target='_blank'>" + prop_value + "</a>";
              popup += "<p style='font-size: 15px;font-family: Arial, Helvetica, sans-serif;font-weight: 300;padding:1px'>" + prop_key + ":" + line + "</p>";
            }else{
              var lines = prop_value.split("\n")
              if(lines.length == 1){
               popup += "<p style='font-size: 15px;font-family: Arial, Helvetica, sans-serif;font-weight: 300;padding:1px'>" + prop_key + ":" + prop_value + "</p>";
              }else{
                //多行文本只显示前5行
                popup += "<p style='font-size: 15px;font-family: Arial, Helvetica, sans-serif;font-weight: 300;padding:1px'>" + prop_key + ":</p>";
                for(let line in lines) {
                  if(line >= 5) break;
                  popup += "<p style='font-size: 15px;font-family: Arial, Helvetica, sans-serif;font-weight: 300;padding:1px;margin-left:20px'>" + lines[line] + "</p>";                  
                }
                if(lines.length > 5 && itemData.props['t_url']){
                  var line = "<a href='" + itemData.props['t_url'] + "' target='_blank'>" + "[全部内容请点击链接查看]" + "</a>";
                  popup += "<p style='font-size: 15px;font-family: Arial, Helvetica, sans-serif;font-weight: 300;padding:1px;margin-left:20px'>" + line + "</p>";
                }
              }
            }
            
          }
          popup += "</div>";
          this.$refs.popup.innerHTML = popup
        }  
      },      
    }

  
</script>
<style scoped>
  .el-dialog .el-input{
    max-width: 300px;
  }
  #chartContainer{
    width: 100%;
    height: 600px
  }
  #popup {
    position:absolute;
    display:none;
    z-index:9999;
    background-color: rgba(255,255,255,0.9);
    min-width: 100px;
    min-height: 50px;
    padding:20px;
    border:1px solid #1888ad;
  }


</style>



